import React, { Component } from "react";
import OfferCard from "./offercard";
import { Item, Grid } from "semantic-ui-react";
import firebase from "../../firebase";
class OfferFileter extends Component {
  constructor(props) {
    super(props);
    this.state = { offers: [] };

    this.offers = null;
  }

  onOfferUpdate = querySnapshot => {
    const offers = [];
    querySnapshot.forEach(doc => {
      const {
        name,
        url,
        price,
        tag,
        description,
        store,
        userid,
        shopname
      } = doc.data();
      const id = doc.id;
      offers.push({
        id,
        name,
        url,
        price, // DocumentSnapshot
        tag,
        store,
        userid,
        description,
        shopname
      });
    });

    this.setState({
      offers
    });
  };

  componentDidMount() {
    firebase
      .firestore()
      .collection(`bazaars/${this.props.id}/offers`)
      .onSnapshot(this.onOfferUpdate);
  }

  render() {
    return (
      <div>
        {/* <h1>Here comes the filter options with title</h1> */}
        <div
          style={{
            paddingTop: "10px",
            borderWidth: "1px",
            borderStyle: "solid",
            borderColor: "#DCDCDC"
          }}
        >
          <Item.Group divided style={{ marginRight: "20px" }}>
            {this.state.offers.map(offer => {
              return (
                <OfferCard
                  key={offer.id}
                  name={offer.name}
                  price={offer.price}
                  tag={offer.tag}
                  description={offer.description}
                  store={offer.store}
                  userid={offer.userid}
                  shopname={offer.shopname}
                  url={offer.url}
                />
              );
            })}
          </Item.Group>
        </div>
      </div>
    );
  }
}

export default OfferFileter;
